import { useEffect, useState, useCallback, useRef } from "react";
import * as Y from "yjs";
import { HocuspocusProvider } from "@hocuspocus/provider";
import { getUserId } from "../utils/userId";
import type {
  CollabSpaceState,
  UpdateFunction,
  CollabSpaceHookReturn,
  CollabSpaceOptions,
} from "../types";

export function useCollabSpace<T extends CollabSpaceState>(
  documentName: string,
  initialState: T,
  options: CollabSpaceOptions = {}
): CollabSpaceHookReturn<T> {
  const [state, setState] = useState<T>(initialState);
  const [isConnected, setIsConnected] = useState(false);
  const [connectionStatus, setConnectionStatus] = useState<
    "connecting" | "connected" | "disconnected" | "error"
  >("disconnected");
  const [userId] = useState(() => getUserId());

  const docRef = useRef<Y.Doc | null>(null);
  const providerRef = useRef<HocuspocusProvider | null>(null);
  const yMapRef = useRef<Y.Map<any> | null>(null);

  const websocketUrl =
    options.websocketUrl || `wss://collab.mexty.ai`;

  // Initialize Yjs document and provider
  useEffect(() => {
    console.log(`🔄 Initializing collaboration for document: ${documentName}`);
    console.log(`🔗 Connecting to: ${websocketUrl}`);

    // Create Y.Doc
    const doc = new Y.Doc();
    docRef.current = doc;

    // Get or create the shared map
    const yMap = doc.getMap("state");
    yMapRef.current = yMap;

    // Initialize with initial state if the map is empty
    if (yMap.size === 0) {
      Object.entries(initialState).forEach(([key, value]) => {
        yMap.set(key, value);
      });
    }

    // Set initial state from Y.Map
    const currentState = {} as T;
    yMap.forEach((value, key) => {
      (currentState as any)[key] = value;
    });
    setState(currentState);

    // Create Hocuspocus provider
    const provider = new HocuspocusProvider({
      url: websocketUrl,
      name: documentName,
      document: doc,
      onOpen: () => {
        console.log(
          `🔌 WebSocket opened for: ${documentName}`
        );
        setConnectionStatus("connecting");
      },
      onConnect: () => {
        console.log(
          `✅ Connected to collaboration server for: ${documentName}`
        );
        setIsConnected(true);
        setConnectionStatus("connected");
        options.onConnect?.();
      },
      onDisconnect: ({ event }) => {
        console.log(
          `❌ Disconnected from collaboration server for: ${documentName}`,
          event
        );
        setIsConnected(false);
        setConnectionStatus("disconnected");
        options.onDisconnect?.();
      },
      onAuthenticationFailed: ({ reason }) => {
        console.error(
          `🚫 Authentication failed for: ${documentName}`,
          reason
        );
        setConnectionStatus("error");
      },
      onSynced: () => {
        console.log(`🔄 Document synced: ${documentName}`);
        // Update state when synced with server
        const syncedState = {} as T;
        yMap.forEach((value, key) => {
          (syncedState as any)[key] = value;
        });
        setState(syncedState);
      },
    });

    providerRef.current = provider;

    // Set connecting status
    setConnectionStatus("connecting");

    // Listen to changes in the Y.Map
    const updateHandler = (event: Y.YMapEvent<any>) => {
      const newState = {} as T;
      yMap.forEach((value, key) => {
        (newState as any)[key] = value;
      });
      setState(newState);
    };

    yMap.observe(updateHandler);

    return () => {
      yMap.unobserve(updateHandler);
      provider.destroy();
      doc.destroy();
    };
  }, [documentName, websocketUrl]);

  const update = useCallback((updateFn: UpdateFunction<T>) => {
    if (!yMapRef.current) return;

    const yMap = yMapRef.current;

    // Get current state from Y.Map
    const currentState = {} as T;
    yMap.forEach((value, key) => {
      (currentState as any)[key] = value;
    });

    // Calculate the update
    const updateData =
      typeof updateFn === "function" ? updateFn(currentState) : updateFn;

    // Apply updates to Y.Map
    Object.entries(updateData).forEach(([key, value]) => {
      if (value !== undefined) {
        yMap.set(key, value);
      }
    });
  }, []);

  return {
    state,
    update,
    isConnected,
    connectionStatus,
    userId,
  };
}
